.thumbnail-detail {
  padding-top: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.detail-card {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  width: 30%;
  border-radius: 4px;
  background: #fefefe;
  -webkit-box-shadow: 0 1px 2px rgba(34, 25, 25, 0.4);
          box-shadow: 0 1px 2px rgba(34, 25, 25, 0.4);
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: start;
}

.container p {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

#upvote {
  display: block;
  height: 24px;
  width: 24px;
  margin-right: 10px;
  background-image: url(../../assets/upvote.svg);
  background-size: 24px 24px;
}

#downvote {
  display: block;
  height: 24px;
  width: 24px;
  margin: 0 10px 0 20px;
  background-image: url(../../assets/downvote.svg);
  background-size: 24px 24px;
}

#score {
  display: block;
  height: 24px;
  width: 24px;
  margin: 0 10px 0 20px;
  background-image: url(../../assets/score.svg);
  background-size: 24px 24px;
}



